    import React, { Suspense, useState } from "react";
    import { Button, Card, CardBody, CardHeader, CardTitle } from "react-bootstrap";
    import TopPerfomingSkeleton from "../../layout/skeleton/TopPerfomingSkeleton";
    import NoData from "../../layout/common/NoData";
    import { SENTIMENTS_CALC } from "../../../../../utils";
    import SeeMoreModal from "../../layout/common/SeeMoreModal";
    import AuthorInfo from "../content/authorInfo";
    import PostMedia from "../../layout/common/postMedia";

    const PostPopup = React.lazy(() => import("../../layout/popup/postPopup"));

    const PerfomingPost = (props) => {
    const { type, title, postData, loader } = props;

    const [show, setShow] = useState(false);

    const handlePopup = (event) => {
        setShow(event);
    };

    return (
        <div className="mb-5">
        <Card className="sw-card-custom gutter-b card-shadow">
            <CardHeader className="p-5">
            <CardTitle className="mb-0 d-flex align-items-center justify-content-between">
                <h6 class="card-label font-weight-bolder m-0">{title}</h6>
                <Button
                variant="link"
                className="p-0"
                onClick={() => handlePopup(true)}
                >
                See More
                </Button>
            </CardTitle>
            </CardHeader>
            <CardBody className="p-5">
            <div className="top_posts_row row">
                {loader ? (
                <TopPerfomingSkeleton />
                ) : postData && Object.keys(postData).length ? (
                postData.map((e) => {
                    return (
                    <div className="top_posts top_posts_new">
                        <div className="card-shadow rounded mb-5">
                        <div className="m_card_cont">
                            <div className="post_img1 cursor-pointer">
                            <PostMedia
                                postFile={e.image}
                                postId={e.id}
                                className={"img-fluid rounded-1"}
                            />
                            </div>
                            <div className="text-center position-relative m_user_info">
                            <div className="top-perfom-post m_grid_author d-flex justify-content-center mb-3">
                                <AuthorInfo
                                isIcon={e.network.icon}
                                picture={e.authorImage}
                                username={e.authorName}
                                date={e.created}
                                />
                                \
                            </div>
                            </div>

                            <div class="post_score mt-4">
                            {type == 1 ? (
                                <div class="d-flex align-items-center justify-content-between py-2">
                                <span class="text-dark font-weight-bold font-size-md">
                                    Impressions
                                </span>
                                <p class="text-dark font-weight-boldest mb-0 d-flex">
                                    {" "}
                                    {e.postPerformance ? e.postPerformance : 0}
                                    <span class="score-count text-danger d-flex align-items-center ms-3">
                                    <svg
                                        class="me-1"
                                        width="18"
                                        height="16"
                                        xmlns="http://www.w3.org/2000/svg"
                                        viewBox="0 0 24 24"
                                        fill="currentColor"
                                    >
                                        <path d="M12 16L6 10H18L12 16Z"></path>
                                    </svg>{" "}
                                    02%
                                    </span>
                                </p>
                                </div>
                            ) : null}

                            <div class="d-flex align-items-center justify-content-between py-2">
                                <span class="text-dark font-weight-bold font-size-md">
                                Post Clicks
                                </span>
                                <p class="text-dark font-weight-boldest mb-0 d-flex">
                                {e.postClicks}{" "}
                                <span class="score-count text-success d-flex align-items-center ms-3">
                                    <svg
                                    class="me-1"
                                    width="18"
                                    height="16"
                                    xmlns="http://www.w3.org/2000/svg"
                                    viewBox="0 0 24 24"
                                    fill="currentColor"
                                    >
                                    <path d="M12 8L18 14H6L12 8Z"></path>
                                    </svg>{" "}
                                    02%
                                </span>
                                </p>
                            </div>
                            <div class="d-flex align-items-center justify-content-between pt-2">
                                <span class="text-dark font-weight-bold font-size-md">
                                Sentiment
                                </span>
                                <div className="d-flex post-status bg-position">
                                <img
                                    className="img-fluid me-2"
                                    src={`https://cloud.tagbox.com/react-app/media/icons/emoji/emoji${
                                    SENTIMENTS_CALC(e.sentiment).item
                                    }.svg`}
                                    width={"16px"}
                                    height={"16px"}
                                />
                                {SENTIMENTS_CALC(e.sentiment).label}
                                </div>
                            </div>
                            </div>
                        </div>
                        </div>
                    </div>
                    );
                })
                ) : (
                <NoData />
                )}
            </div>
            </CardBody>
        </Card>

        <SeeMoreModal
            size={"lg"}
            showHide={show}
            handlePopup={handlePopup}
            title={"Top Perfoming Posts"}
        >
            <Suspense fallback={<></>}>
            <PostPopup {...props} />
            </Suspense>
        </SeeMoreModal>
        </div>
    );
    };

    export default React.memo(PerfomingPost);
